.tools{ 
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 16px;
    margin-bottom: 0.5rem;
}
.tool-row{
    flex:1;
    display:flex;
    flex-direction:column;
    gap:1rem;
}

.tool-lineitems{
    flex:1;
    display:flex;
    flex-direction:row;
    align-items: center;
    gap:0.5rem;
}

.toolLogo{
    width: 1.6rem;
    border-radius: 0.2rem;
    max-height: 24px;
    max-width: 24px;
    aspect-ratio: 1;
    padding: 0.1rem;
}

.CertLogo{
    width: 2rem;
    border-radius: 0.24rem;
    max-height: 42px;
    max-width: 42px;
    aspect-ratio: 1;
    padding: 0.1rem;
}


.resourcesButton{
    display: flex;
    background-color: var(--text-color3);
    height: 4rem;
    border-radius: 0.5rem;
    backdrop-filter: blur(5px);
    border: 0rem;
    margin-bottom: 2rem;
    margin-top: 2rem;
    align-items: center;
    padding: 1rem;
    font-size: medium;
    font-weight: 800;
    color: var(--text-color0);
    justify-content: space-between;
    width: 100%;
}

.resourcesButton:hover{
    background-color:var(--text-color2);
    backdrop-filter: blur(0px);
}