.link{
    text-decoration: none;
    color: var(--text-color6);
    font-weight:700
}

.link:hover{
    text-decoration: underline;
    color: #ff7744f0;
    font-weight:700
}

.col{
    max-width: 864px;
    display: flex;
    justify-content: flex-start;
    margin: 3rem 1rem 8rem 1rem;
}
.row{
    flex-direction: column;
    justify-content: flex-start;
    gap: 32px;
}

.closeIcon{
    background-color: transparent;
    display:flex;
    flex-direction:row;
    align-items:center;
    gap:0.5rem;
    padding:1px;
    border-radius: 0.25rem;
    border: 1px  solid var(--text-color5);
}

.closeIcon:hover{
    background-color: var(--text-color2);
}

.spinnerFirstOverlay{
    height:100vh;
    width:100vw;
    position:fixed;
    margin:auto;
    z-index: 1001;
    background:#121212;
    animation-iteration-count: 1;
    animation: fromtop1 ease-in 4s;
    animation-fill-mode: forwards;
    -webkit-animation-fill-mode: forwards;

}

.spinnerSecondOverlay{
    height:100vh;
    width:100vw;
    position:fixed;
    margin:auto;
    z-index: 1002;
    margin-top: -1%;
    background:#2d2d2d;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation: fromtop2 ease-in 4.2s;
    -webkit-animation-fill-mode: forwards;
}

.spinnerMain{
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation: fade ease-in 3.2s;
    
    -webkit-animation-iteration-count:1;
    -webkit-animation: fade ease-in 3.2s;
    -webkit-animation-fill-mode: forwards;
}

.spinnerVideo{
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation: fade ease-in 2.6s;
    
    -webkit-animation-iteration-count:1;
    -webkit-animation: fade ease-in 2.6s;
    -webkit-animation-fill-mode: forwards;
}

@keyframes fade {
    0% {
       
        opacity: 100%;
    }
    80% {
    
        opacity: 100%;
    }
     100%{
        opacity: 0%;
     }
}


@keyframes fromtop1 {
    0% {
        height: 0%;
        opacity: 100%;
    }
    50% {
        height: 0%;
        opacity: 100%;
    }

    85% {
        height: 145%;
        opacity: 100%;
     }
     100%{
        height: 145%;
        opacity: 0%;
     }
}

@keyframes fromtop2 {
    0% {
        height: 0%;
        opacity: 100%;
    }
    60% {
        height: 0%;
        opacity: 100%;
    }

    85% {
        height: 145%;
        opacity: 100%;
     }
     100%{
        height: 145%;
        opacity: 0%;
     }
}


.littleThingsCard{

    background-color: var(--bg-color4);
    border-radius: 0.5rem;
    margin-bottom:2rem;
    padding: 1rem;
    display: grid;
    flex-direction: row;
    gap:0.6rem;
    border:0.5px var(--bg-color4) solid;
}


.ender{
    stroke: var(--bg-color5);
}


/* Ripple container */
.ripple {
    position: fixed; /* Fixed to viewport */
    top: 50%; /* Center vertically */
    left: 50%; /* Center horizontally */
    width: 2px; /* Initial diameter = 2px */
    height: 2px;
    background: rgba(0, 0, 0, 1); /* Adjust for light/dark mode */
    border-radius: 50%; /* Makes it circular */
    transform: scale(0); /* Start small */
    pointer-events: none; /* Prevent interaction blocking */
    z-index: 9999; /* Ensure it appears above other elements */
    transform-origin: center; /* Scale from center */
    animation: rippleEffect 1s ease-out forwards; /* Trigger animation */
}

/* Ripple animation */
@keyframes rippleEffect {
    from {
        transform: scale(0); /* Start at original size (2px diameter) */
        opacity: 1; /* Fully visible */
    }
    to {
        transform: scale(150); /* Expand outward to cover full screen */
        opacity: 1; /* Fade out gradually (optional) */
    }
}