@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Space+Mono:wght@300;400;500;600;700&display=swap');


:root{

  --bg-color1:#000000;
  --bg-color1 : black;
  --bg-color2 : #171717; 
  --bg-color3: #101010;
  --bg-color4: #1a1a1a;
  --bg-color5: #ffffff40;

  --bg-mainBG: #0d0d0d;
  --bg-mainBGinv: red;

  --text-color0: #fff;
  --text-color1: #fffffff0;
  --text-color2: #ffffff40;
  --text-color3: #ffffff20;
  --text-color4:#bbbbbb;
  --text-color5:#ffffff80;
  --text-color6:#ffffffd0;
  --text-color7:#ffffff60;
  --text-color8:#ffffff10;
 
}

[data-theme="light"] {
 
  --bg-color1:#e4e4e4;
  --bg-color1 : white;
  --bg-color2 : #fefefe; 
  --bg-color3: #f1f0f0;
  --bg-color4: #e9e9e9;
  --bg-color5: #00000020;

  --bg-mainBG: #fcfcfc;
  --bg-mainBGinv: yellow;

  --text-color0: #000;
  --text-color1: #000000f0;
  --text-color2: #00000040;
  --text-color3: #00000020;
  --text-color4:#272727;
  --text-color5:#00000080;
  --text-color6:#000000d0;
  --text-color7:#00000060;
  --text-color8:#00000010;
}


*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'space Grotesk',sans-serif;
  --plyr-color-main: #ff7744f0;
 cursor: url(../src//assets/customCursor.png),auto !important;
 list-style: none;
}

body{
  overflow-y: scroll;
}

button:hover,a:hover, .closeIcon:hover, .closeIcon path:hover,.musicIcon:hover,.musicIcon path:hover,.cardLib:hover,.cardLib *:hover,.backButton *:hover{
  cursor: url(../src//assets/hover.png),auto !important;
}
a *:hover,button *:hover,a.svg:hover,a span:hover,div.writingItem, div.writingItem *{
  cursor: url(../src//assets/hover.png),auto !important;
}

figure .plyr{
  border-radius: 0.5rem ;
}

.home, .works, .writings, .otherActivities,.projectDetails,.writingDetails,.PageNotFound,.DesignLibrary{
  display: flex;
  height: auto;
  justify-content: center;

}

p{
  font-size:16px;
  font-weight:400;
  color:var(--text-color4);
  line-height:1.6rem;
  white-space: pre-line;  
}

h1{
  font-weight:600;
color:var(--text-color1);
line-height:1.6rem;
}

h2{
  font-weight:600;
  color:var(--text-color1);
  line-height:1.6rem;
}

h3{
  font-weight:600;
  color:var(--text-color1);
  line-height:1.6rem;
}
h4{
  font-size: medium;
  font-weight:500;
  color:var(--text-color6);
  line-height:1.6rem;
}


h6{
  font-size:16px;
  font-weight:400;
  color: var(--text-color5);
}

a {
  text-decoration: none;
  text-decoration-color :initial;
  color: initial;
}

hr{
  margin: 1rem 0px;
  opacity: 20%;
  z-index: -1;
}

hr.dot::before{
  content: "...";
}

blockquote{
  color: var(--text-color6);
  border-left: var(--text-color1) solid 4px;
  padding: 1rem;
  background-color: var(--text-color3);
}

code{
background-color: red;
}





.HeaderFixed{
  background-color: transparent;
  display:flex;
  flex-direction: column;
  gap:1rem;
  margin-bottom: 1rem;
  z-index: 100;
  display: none;
}
.HeaderSticky{
  background-color: var(--bg-mainBG);
  border-bottom: 2px var(--text-color3) solid;
  height: auto;
  display:flex;
  gap:1rem;
  align-items:center;
  position: sticky;
  top: 0%;
  z-index: 100;
  padding: 0.5rem 0rem;
  
}

.HeaderSticky h2{
  font-size: 16px;
}

.back{
    display:none;
}

.primaryBtn{
  background-color: transparent;
  
  display:flex;
  flex-direction:row;
  align-items:center;
  gap:0.5rem;
  padding:0.5rem 0.5rem;
  border-radius: 0.25rem;
  border: 1px  solid transparent;
  color: var( --text-color0);
  margin:0.5rem auto;
  align-self: center;
}

.primaryBtn:hover{
  background-color: var(--text-color3);

}

.fillBtn{
  background-color: var(--text-color3);
  
  display:flex;
  flex-direction:row;
  align-items:center;
  gap:0.5rem;
  padding:0.5rem 0.5rem;
  border-radius: 0.25rem;
  border: 1px  solid transparent;
  color: var( --text-color0);
  margin:0.5rem auto;
  align-self: center;
}

.fillBtn:hover{
  background-color: #ff7744;
  
  display:flex;
  flex-direction:row;
  align-items:center;
  gap:0.5rem;
  padding:0.5rem 0.5rem;
  border-radius: 0.25rem;
  border: 1px  solid transparent;
  color: var( --text-color0);
  margin:0.5rem auto;
  align-self: center;
}

.backButton{
  background-color: transparent;
      display:flex;
      flex-direction:row;
      align-items:center;
      gap:0.5rem;
      padding:0.2rem 0.5rem;
      border-radius: 0.25rem;
      border: 1px  solid var(--text-color5);
}





.backButton:hover{
  background-color: var(--text-color2);
}

body .spline-watermark{
  display: none;
}


/* Scrollbar */
body::-webkit-scrollbar {
  width: 0.8rem;            /* width of the entire scrollbar */
}

body::-webkit-scrollbar-track {
background: #030303;        /* color of the tracking area */
}

body::-webkit-scrollbar-thumb {
background-color: #333333;   /* color of the scroll thumb */
border-radius: 20px; 
height:1rem; 
    /* roundness of the scroll thumb */
}
body::-webkit-scrollbar-button{
scrollbar-width:1rem; 
}

.overview{
  box-shadow: 
    inset 0 1px 0px 0px var(--bg-color4), /* Inner shadow - subtle top shadow */
    inset 0 0px 0px 1px var(--bg-color4), /* Inner shadow - spread effect */
    0 0px 1px 1px var(--bg-color3), /* Drop shadow layer 1 */
    0 1px 1px 0px var(--bg-color3); /* Drop shadow layer 2 */ /* Drop shadow layer 3 */
  /* Drop shadow layer 4 */
}
